import auth from '../../common/auth.js';
export default {
	data() {
		return {
			id: null,
			form: {},
			isSuccess: true,
			errorMsg: ''
		};
	},
	onShow() {

	},
	onLoad(option) {
		this.form = {}
		this.id = option.id;
		this.$api
			.ScanToGetDisheAsync({
				OrderGuid: option.id
			})
			.then(res => {
				if (res.code == 0) {
					res.data.totalPrice = res.data.totalPrice.toFixed(2);
					res.data.dishesDetail.forEach(item => {
						item.unitPrice = item.unitPrice.toFixed(2);
					});
					this.form = res.data;
					this.isSuccess = true;
				} else {
					this.form = {}
					this.isSuccess = false;
					this.errorMsg = res.message
				}
			})
			.catch(err => {});
	},
	methods: {
		select() {
			let t = this;
			uni.scanCode({
				onlyFromCamera: true,
				success: function(res) {
					console.log(res);
					uni.navigateTo({
						url: '/pages/veriy/index?id=' + res.result
					});
				},
				fail: function(err) {
					console.log(err);
				}
			});
		},
	}
};
